body {
  color: #eef1ef;
  background-color: #2b2b2b;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 14px;
  line-height: 1.5;
}

* {
  box-sizing: border-box;
}

button {
  color: #2b2b2b;
  cursor: pointer;
  background-color: #ffa07a;
  border: 2px solid #ffa07a;
  border-radius: 6px;
  padding: 4px 8px;
  font-size: 14px;
}

button.cta {
  padding: 6px 14px;
  transition: border-radius .2s ease-in-out;
}

button.cta:hover {
  border-radius: 8px;
}

.logo {
  text-align: center;
  margin-top: 128px;
  margin-bottom: 12px;
  font-size: 38px;
  font-weight: 900;
}

.logo span {
  color: #dcc6e0;
  font-weight: normal;
}

.description {
  text-align: center;
  max-width: 450px;
  margin: 0 auto 100px;
  font-size: 14px;
}

.columns {
  max-width: 800px;
  margin: 0 auto;
  display: flex;
}

.columns > * {
  flex: 1;
  width: 50%;
}

.columns pre {
  margin: 0;
}

#counter {
  justify-content: center;
  align-items: center;
  width: 60%;
  display: flex;
}

#counter button {
  cursor: pointer;
  color: #ffa07a;
  will-change: transform, box-shadow;
  background-color: #242424;
  border: 1px solid #ffa07a;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  font-size: 16px;
  font-weight: bold;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  display: flex;
}

#counter button:hover {
  transform: scale(1.05);
  box-shadow: 0 0 10px #000000bf;
}

#counter button + div {
  padding: 18px 0 20px;
  font-size: 72px;
  font-weight: 900;
  line-height: 1;
}

#counter > div {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.content {
  width: 500px;
  margin-left: 230px;
  padding: 18px;
}

.hljs {
  background-color: #242424;
}

